import React from 'react';

import './App.css';
import {Switch, Route} from 'react-router-dom';
import ProjectCard from './ProjectCard';
import ProjectArray from './Projects'
import About from './About';

function Main() {
    return (
        <Switch>
            <Route exact path="/" component = {About}>
            </Route>
            <Route  path="/projects" render = {(props) => <ProjectCard {... props}projectArray = {ProjectArray}/>}/>
            <Route  path="/resume">
                Resume
            </Route>
            <Route  path="/contact">
                Contact
            </Route>
      </Switch>
    )
}
export default Main;